import { generalGuidelines, portions } from "@/lib/data/guidelines";

export default function RecomendacionesPage() {
  return (
    <div className='container py-6'>
      <h1 className='text-3xl font-bold mb-6'>Recomendaciones Generales</h1>

      {/* Porciones personalizadas */}
      <section className='mb-10'>
        <h2 className='text-2xl font-semibold mb-4'>Porciones Recomendadas</h2>
        <div className='grid gap-6 md:grid-cols-2'>
          {portions.map((person) => (
            <div
              key={person.name}
              className='p-6 bg-card text-card-foreground rounded-lg border'
            >
              <h3 className='text-xl font-semibold mb-4'>
                Porciones para {person.name}
              </h3>
              <dl className='space-y-2'>
                {Object.entries(person.portions).map(([key, value]) => (
                  <div key={key} className='flex justify-between'>
                    <dt className='font-medium capitalize'>{key}:</dt>
                    <dd className='text-muted-foreground'>{value}</dd>
                  </div>
                ))}
              </dl>
            </div>
          ))}
        </div>
      </section>

      {/* Recomendaciones por categoría */}
      <section className='space-y-6'>
        {["cooking", "portions", "hydration", "exercise", "general"].map(
          (category) => {
            const categoryGuidelines = generalGuidelines.filter(
              (g) => g.category === category
            );

            if (categoryGuidelines.length === 0) return null;

            return (
              <div key={category} className='space-y-4'>
                <h2 className='text-2xl font-semibold capitalize'>
                  {category === "cooking"
                    ? "Cocina y Preparación"
                    : category === "portions"
                      ? "Porciones y Cantidades"
                      : category === "hydration"
                        ? "Hidratación"
                        : category === "exercise"
                          ? "Ejercicio Físico"
                          : "Recomendaciones Generales"}
                </h2>
                <div className='grid gap-4'>
                  {categoryGuidelines.map((guideline) => (
                    <article
                      key={guideline.id}
                      className='p-6 bg-card text-card-foreground rounded-lg border'
                    >
                      <h3 className='text-xl font-semibold mb-3'>
                        {guideline.title}
                      </h3>
                      <ul className='list-disc pl-5 space-y-2'>
                        {guideline.content.map((item, index) => (
                          <li key={index} className='text-muted-foreground'>
                            {item}
                          </li>
                        ))}
                      </ul>
                      {guideline.tips && (
                        <div className='mt-4'>
                          <h4 className='font-semibold mb-2'>Consejos:</h4>
                          <ul className='list-disc pl-5 space-y-1'>
                            {guideline.tips.map((tip, index) => (
                              <li key={index} className='text-muted-foreground'>
                                {tip}
                              </li>
                            ))}
                          </ul>
                        </div>
                      )}
                    </article>
                  ))}
                </div>
              </div>
            );
          }
        )}
      </section>
    </div>
  );
}
